'use client';

import { ChevronRight } from 'lucide-react';
import Image from 'next/image';

interface Movie {
    id: number;
    title: string;
    image: string; // URL
    isNew?: boolean;
}

interface MovieRowProps {
    title: string;
    movies: Movie[]; // In real app, this would be passed or fetched
}

// Mock Data (will be replaced by props later if needed or kept for demo)
const MOCK_MOVIES = [
    { id: 1, title: 'Avatar: Fogo e Cinzas', image: 'https://images.unsplash.com/photo-1536440136628-849c177e76a1?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 2, title: 'Truque de Mestre', image: 'https://images.unsplash.com/photo-1626814026160-2237a95fc5a0?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 3, title: 'SISU', image: 'https://images.unsplash.com/photo-1595769816263-9b910be24d5f?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 4, title: 'Zootopia 2', image: 'https://images.unsplash.com/photo-1608889476561-6242cfdbf622?auto=format&fit=crop&w=300&q=80' },
    { id: 5, title: 'Five Nights at Freddys', image: 'https://images.unsplash.com/photo-1509347528160-9a9e33742cd4?auto=format&fit=crop&w=300&q=80', isNew: true },
    { id: 6, title: 'O Brilho do Diamante', image: 'https://images.unsplash.com/photo-1510511459019-5dda7724fd87?auto=format&fit=crop&w=300&q=80' },
    { id: 7, title: 'Example Movie', image: 'https://images.unsplash.com/photo-1485846234645-a62644f84728?auto=format&fit=crop&w=300&q=80' },
];

export default function MovieRow({ title }: { title: string }) {
    return (
        <div className="mb-10 pl-6 md:pl-12">
            <div className="flex items-center justify-between mb-4 pr-6 md:pr-12">
                <h2 className="text-xl md:text-2xl font-bold text-white">{title}</h2>
                <button className="text-sm text-green-400 hover:text-green-300 flex items-center transition-colors">
                    Ver todos <ChevronRight size={16} />
                </button>
            </div>

            <div className="flex space-x-4 overflow-x-auto pb-4 scrollbar-hide snap-x">
                {MOCK_MOVIES.map((movie) => (
                    <div key={movie.id} className="relative flex-none w-[160px] md:w-[200px] aspect-[2/3] group cursor-pointer snap-start">
                        <div className="w-full h-full rounded-lg overflow-hidden relative transition-transform duration-300 group-hover:scale-105 group-hover:z-10 shadow-lg group-hover:shadow-2xl border border-white/5">
                            {/* Image Placeholder */}
                            <div className="w-full h-full bg-gray-800 relative">
                                <Image
                                    src={movie.image}
                                    alt={movie.title}
                                    fill
                                    className="object-cover"
                                    unoptimized // For external images in demo
                                />
                            </div>

                            {/* Overlay/Gradient on Hover */}
                            <div className="absolute inset-0 bg-black/60 opacity-0 group-hover:opacity-100 transition-opacity flex flex-col justify-end p-3">
                                <span className="text-sm font-bold text-white mb-1 drop-shadow-md">{movie.title}</span>
                                <span className="text-xs text-gray-300">2024 • 2h 10m</span>
                            </div>
                        </div>

                        {movie.isNew && (
                            <div className="absolute top-2 left-2 bg-green-500 text-black text-[10px] font-bold px-1.5 py-0.5 rounded shadow-md z-20">
                                NOVO
                            </div>
                        )}

                        <p className="mt-2 text-xs text-gray-400 text-center truncate w-full group-hover:text-white transition-colors">{movie.title}</p>
                    </div>
                ))}
            </div>
        </div>
    );
}
